import React, { useState } from 'react';
import {
  Box,
  Container,
  Paper,
  Typography,
  Button,
  TextField,
  Divider,
  InputAdornment,
} from '@mui/material';
import { Google, Phone,WhatsApp,Password} from '@mui/icons-material';
import KeyIcon from '@mui/icons-material/Key';
import { MuiTelInput, matchIsValidTel  } from "mui-tel-input";
import { useNavigate } from 'react-router-dom';
import { login, verify_phone } from '../../api/user_account';
import Notification from './components/Notification';

export default function LoginPage() {
  const [phoneNumber, setPhoneNumber] = useState('');
  const [passWord, setPassWord] = useState('');
  const [step, setStep] = useState('phone');
  const navigate=useNavigate();
  const [showNotification, setShowNotification] = useState(false);
  const [error, setError] = useState("");

  const handleGoogleLogin = () => {
    console.log('Google login clicked');
  };

  const handlePhoneSubmit = async (e) => {
    e.preventDefault();
    console.log('Phone:', phoneNumber);
    if (!matchIsValidTel(phoneNumber)) {
      setError("Please enter a valid phone number");
      return;
    }
    setError(""); 
    try{
    // await verify_phone({"phoneNumber":phoneNumber});
    setStep('password');
    }catch(error){
      alert('Failed to send phone number.Please try again.')
    }

  };

  const handlePassWordSubmit = async(e) => {
    e.preventDefault();
    try{
      const response=await login({"phoneNumber":phoneNumber,"passWord":passWord})
      if(response===200)
        {
          setShowNotification(true);
          setTimeout(() => {
            navigate('/home');
          }, 1000);
     
        }else{
        alert('Password is incorrect. Please try again.',response)
      }
    }catch(error){
      alert(error)
    }
  };

  return (
    <Box
      sx={{
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: 2,
      }}
    >
      <Container maxWidth="sm">
        <Paper
          elevation={24}
          sx={{
            p: 4,
            backgroundColor: 'background.paper',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            borderRadius: 2,
          }}
        >
          <Typography
            variant="h4"
            component="h1"
            align="center"
            sx={{
              mb: 4,
              color: 'primary.main',
              fontWeight: 600,
              letterSpacing: '-0.02em',
            }}
          >
            rife.
          </Typography>

          <Button
            fullWidth
            variant="outlined"
            startIcon={<Google />}
            onClick={handleGoogleLogin}
            sx={{
              mb: 3,
              backgroundColor: 'rgba(255, 255, 255, 0.05)',
              borderColor: 'rgba(255, 255, 255, 0.1)',
              color: 'white',
              '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.1)',
                borderColor: 'rgba(255, 255, 255, 0.2)',
              },
              textTransform: "none",
              fontSize: "1rem",
              padding: "12px 16px ",
              fontWeight: "bold",
            }}

          >
            Continue with Google
          </Button>

          <Divider sx={{ mb: 3 }}>
            <Typography variant="body2" color="text.secondary">
              or
            </Typography>
          </Divider>

          {step === 'phone' ? (
            <form onSubmit={handlePhoneSubmit}>
              <MuiTelInput
                defaultCountry="GB" // Default country set to UK
                value={phoneNumber}
                onChange={setPhoneNumber}
                fullWidth
                label="Phone Number"
                variant="outlined"
                sx={{ mb: 2 }}
                error={!!error}
                helperText={error}
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                startIcon={<Phone />}
                sx={{
                  textTransform: "none",
                  fontSize: "1rem",
                  padding: "12px 16px ",
                  fontWeight: "bold",
                }}
              >
                Continue with Phone
              </Button>
            </form>
          ) : (
            <form onSubmit={handlePassWordSubmit}>
              <TextField
                fullWidth
                label="Enter Password"
                variant="outlined"
                value={passWord}
                onChange={(e) => setPassWord(e.target.value)}
                sx={{ mb: 2 }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <KeyIcon  />
                    </InputAdornment>
                  ),
                }}
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{
                  textTransform: "none",
                  fontSize: "1rem",
                  padding: "12px 16px ",
                  fontWeight: "bold",
                }}
              >
                Login
              </Button>
            </form>
          )}

            <Button
              fullWidth
              variant="outlined"
              startIcon={<WhatsApp style={{ color: "green" }} />}
              sx={{
                textTransform: "none",
                fontSize: "1rem",
                padding: "12px 16px ",
                fontWeight: "bold",
                marginTop: "16px", 
              }}
    
              href="whatsapp://send?text=Discover Rife, an amazing way to make payments! https://rifeapp.github.io/io/ "
            >
              Share via WhatsApp
            </Button>

          <Typography
            variant="body2"
            align="center"
            sx={{
              mt: 3,
              color: 'text.secondary',
              fontSize: '0.875rem',
            }}
          >
            By continuing, you agree to our Terms of Service and Privacy Policy
          </Typography>

        </Paper>
           {showNotification && 
                <Notification
                  message="Login successful!"
                  open={showNotification}
                  onClose={() => setShowNotification(false)}
                />}
      </Container>

    </Box>
  );
}

